.free-delivery {
	width: 100%;
	padding: 30px 0;
	.row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		padding: 30px 0;
		&:last-child {
			.left {
				padding-right: 0;
				padding-left: 50px; } } }
	.left {
		width: 60%;
		padding-right: 50px; }
	&__img {
		display: block;
		width: 40%;
		box-shadow: -12px 12px 56px 6px rgba(0, 0, 0, 0.09); }
	.title {
		font-family: 'Montserrat', sans-serif;
		color: $dark;
		margin-bottom: 20px;
		font-size: 25px;
		font-weight: 900;
		span {
			display: block;
			text-transform: uppercase;
			width: 100%;
			font-size: 25px;
			color: $orange; } } }
